@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
}


.img img{
  width:10%;
  margin-bottom:10px;
}
body {
  font-family: "Montserrat", sans-serif;
  background: -webkit-linear-gradient(340deg, #f93d66,#6d47d9);
  background: linear-gradient(110deg, #f93d66,#6d47d9);
}

.App {
  min-height: 100vh;
  background: white;
  padding:50px;
  
}

.header {
  text-align: center;
  padding: 20px 40px 60px 40px;
}

.title {
  font-weight: 200;
  font-size: 5rem;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 40px;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.search-filter {
  max-width: 1000px;
  margin: 0 auto;
}

.search-bar {
  max-width: 500px;
  min-width: 400px;
  width: 45%;
  border: none;
  padding: 12px;
  box-shadow: 0 0 5px 0 #00000040;
  border-radius: 20px;
}

.search-bar::-webkit-input-placeholder {
  color: #00000070;
}

.search-bar::-moz-placeholder {
  color: #00000070;
}

.search-bar:-ms-input-placeholder {
  color: #00000070;
}

.search-bar::-ms-input-placeholder {
  color: #00000070;
}

.search-bar::placeholder {
  color: #00000070;
}

.search-button {
  background-image: -webkit-gradient(linear, left top, right top, from(#f78ca0), color-stop(19%, #f9748f), color-stop(60%, #fd868c), to(#fe9a8b));
  background-image: -webkit-linear-gradient(left, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
  background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
  border: 1px solid #fe9a8b;
  padding: 12px 15px;
  color: white;
  box-shadow: 0 0 5px 0 #00000040;
  border-radius: 20px;
  position: relative;
  right: 43px;
  top: -1px;
}

.search-bar:focus,
.search-bar:hover {
  box-shadow: 0 0 5px 2px #00000040;
  outline: none;
}

.search-button:focus {
  outline: none;
}

.totalNutrition{
    display:grid;
    grid-template-areas: 
    "proteins fats carbs";
    grid-gap:20px;
}

.total-proteins{
    grid-area:proteins;
}
.total-fats{
    grid-area: fats;
}
.total-carbs{
    grid-area: carbs;
}
.total-nutrition{
    padding:10px;
    text-align:center;
    box-shadow: 0 0 4px rgba(0,0,0,0.1);
}
.list{
    display:grid;
    text-align:center;
    padding:20px;
    grid-template-columns: 1fr 1fr;
    box-shadow: 0 0 4px rgba(0,0,0,0.1);    
}

.left{
    display:grid;
    padding-right:50px;
    justify-items:end;
}

.left-list{
    list-style: none;
}
.right{
    display:grid;
    padding-right:50px;
    justify-items:start;
}
.plate{
    grid-column: span 2;
}
.food{
    display:grid;
    grid-template-areas: 
    "name name name"
    "proteins fats carbs";
    grid-gap:20px;
}

.name{
    grid-area:name;
}
.proteins{
    grid-area:proteins;
}
.fats{
    grid-area: fats;
}
.carbs{
    grid-area: carbs;
}

.add-button {
    background-image: -webkit-gradient(linear, left top, right top, from(#f78ca0), color-stop(19%, #f9748f), color-stop(60%, #fd868c), to(#fe9a8b));
    background-image: -webkit-linear-gradient(left, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
    background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
    border: 1px solid #fe9a8b;
    padding: 12px 15px;
    color: white;
    box-shadow: 0 0 5px 0 #00000040;
    border-radius: 20px;
    position: relative;
   
  }

  .add-button:focus,
.add-button:hover {
  box-shadow: 0 0 5px 2px #00000040;
  outline: none;
}

.search-button:focus {
  outline: none;
}


.food-nutrition{
    padding:10px;
    text-align:center;
    box-shadow: 0 0 4px rgba(0,0,0,0.1);

}

.icon{
    font-size:50px;
}
