.totalNutrition{
    display:grid;
    grid-template-areas: 
    "proteins fats carbs";
    grid-gap:20px;
}

.total-proteins{
    grid-area:proteins;
}
.total-fats{
    grid-area: fats;
}
.total-carbs{
    grid-area: carbs;
}
.total-nutrition{
    padding:10px;
    text-align:center;
    box-shadow: 0 0 4px rgba(0,0,0,0.1);
}