.list{
    display:grid;
    text-align:center;
    padding:20px;
    grid-template-columns: 1fr 1fr;
    box-shadow: 0 0 4px rgba(0,0,0,0.1);    
}

.left{
    display:grid;
    padding-right:50px;
    justify-items:end;
}

.left-list{
    list-style: none;
}
.right{
    display:grid;
    padding-right:50px;
    justify-items:start;
}
.plate{
    grid-column: span 2;
}