@import url("https://fonts.googleapis.com/css?family=Montserrat&display=swap");

* {
  box-sizing: border-box;
}


.img img{
  width:10%;
  margin-bottom:10px;
}
body {
  font-family: "Montserrat", sans-serif;
  background: linear-gradient(110deg, #f93d66,#6d47d9);
}

.App {
  min-height: 100vh;
  background: white;
  padding:50px;
  
}

.header {
  text-align: center;
  padding: 20px 40px 60px 40px;
}

.title {
  font-weight: 200;
  font-size: 5rem;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 40px;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.search-filter {
  max-width: 1000px;
  margin: 0 auto;
}
