.food{
    display:grid;
    grid-template-areas: 
    "name name name"
    "proteins fats carbs";
    grid-gap:20px;
}

.name{
    grid-area:name;
}
.proteins{
    grid-area:proteins;
}
.fats{
    grid-area: fats;
}
.carbs{
    grid-area: carbs;
}

.add-button {
    background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
    border: 1px solid #fe9a8b;
    padding: 12px 15px;
    color: white;
    box-shadow: 0 0 5px 0 #00000040;
    border-radius: 20px;
    position: relative;
   
  }

  .add-button:focus,
.add-button:hover {
  box-shadow: 0 0 5px 2px #00000040;
  outline: none;
}

.search-button:focus {
  outline: none;
}


.food-nutrition{
    padding:10px;
    text-align:center;
    box-shadow: 0 0 4px rgba(0,0,0,0.1);

}

.icon{
    font-size:50px;
}